  const copyCoordinates = () => {
    if (searchResult) {
      const coordinates = `${searchResult.lat}, ${searchResult.lng}`;
      navigator.clipboard.writeText(coordinates).then(() => {
        alert('  !');
      }).catch(() => {
        //  API   
        const textArea = document.createElement('textarea');
        textArea.value = coordinates;
        document.body.appendChild(textArea);
        textArea.select();
        document.execCommand('copy');
        document.body.removeChild(textArea);
        alert('  !');
      });
    }
  };

  //   Property   
  const convertExcelDataToProperties = (excelData: any[][]): Property[] => {
    const headers = excelData[0];
    const rows = excelData.slice(1);
    
    //    
    const propertyNumberIndex = headers.findIndex(header => 
      header?.toLowerCase().includes('') || 
      header?.toLowerCase().includes('')
    );
    
    console.log('  :', propertyNumberIndex);
    
    return rows.map((row, index) => {
      //   -     
      let propertyNumber = '';
      
      if (propertyNumberIndex >= 0 && row[propertyNumberIndex]) {
        //      
        propertyNumber = row[propertyNumberIndex].toString().trim();
        console.log(` ${index + 1} : ${propertyNumber}`);
      } else if (row[0]) {
        //      
        propertyNumber = row[0].toString().trim();
        console.log(` ${index + 1}    : ${propertyNumber}`);
      } else {
        //  
        propertyNumber = `0000${String(index + 1).padStart(3, '0')}`;
        console.log(` ${index + 1}  : ${propertyNumber}`);
      }
      
      const property: Property = {
        id: propertyNumber, //  ID 
        title: row[1] || ` ${propertyNumber}`,
        description: row[2] || '',
        price: parseFloat(row[9]?.toString().replace(/[^0-9]/g, '')) || 0,
        deposit: parseFloat(row[10]?.toString().replace(/[^0-9]/g, '')) || 0,
        type: row[8]?.toLowerCase().includes('') ? 'sale' : 'rent',
        propertyType: row[7]?.toLowerCase().includes('') ? 'apartment' : 
                     row[7]?.toLowerCase().includes('') ? 'apartment' : 'house',
        address: row[4] || '',
        location: { 
          lat: parseFloat(row[5]) || 0, 
          lng: parseFloat(row[6]) || 0 
        },
        bedrooms: parseInt(row[16]?.toString().split('/')[0]) || 1,
        bathrooms: parseInt(row[16]?.toString().split('/')[1]) || 1,
        area: parseFloat(row[13]?.toString().split('/')[0]) * 3.3058 || 0, //  m 
        images: ['https://via.placeholder.com/300x200'],
        contact: {
          name: row[22] || ' ', //  
          phone: row[23] || '02-0000-0000', //  
          email: row[24] || 'registered@realestate.com' //  
        },
        features: [],
        createdAt: new Date(),
        isActive: true,
        confirmedDate: row[25] || '25.07.19' //  
      };
      
      console.log(` : ${propertyNumber} - ${property.title}`);
      return property;
    });
  };

